<template>
  <div>
    <form @submit.prevent="handleSubmit">
      <input
        type="text"
        class="input"
        :value="props.value"
        @input="handleChange"
        :placeholder="props.placeholder"
        ref="input"
      />
      <span class="bottom"></span>
      <span class="right"></span>
      <span class="top"></span>
      <span class="left"></span>
    </form>
  </div>
</template>

<script setup lang="ts">
import { ref } from "vue";

const props = defineProps({
  /**
   * Actual value to display
   */
  value: {
    type: String,
    required: false,
    default: "",
  },

  /**
   * Actual value to display
   */
  placeholder: {
    type: String,
    required: false,
    default: "",
  },
  /**
   * Callback function on click
   */
  onChangeCallback: {
    type: Function,
    required: false,
  },
  /**
   * Callback function on click
   */
  onSubmitCallback: {
    type: Function,
    required: false,
  },
});
const input = ref(null);
const handleChange = (event: Event) => {
  const target = event.target as HTMLSelectElement;
  if (props.onChangeCallback) {
    props.onChangeCallback(target.value);
  }
};

const handleSubmit = () => {
  if (props.onSubmitCallback && input.value?.value !== undefined) {
    props.onSubmitCallback(input.value.value);
  }
};
</script>

<style scoped>
div {
  position: relative;
  width: 160px;
  height: 40px;
}
input {
  background-color: var(--grey-dark3);
  height: 40px;
  border: solid 1px;
  color: var(--white);
  border: none;
  outline: none;
  width: 160px;
  height: 40px;
  border-radius: 5px;
  opacity: 1;
  text-align: left;
  border: 1px solid transparent;
  transition: background-color 0.3s ease-in-out;
}
input:focus {
  outline: none;
}

input::placeholder {
  color: hsla(0, 0%, 100%, 0.6);
}

span {
  position: absolute;
  background-color: var(--primary);
  transition: transform 0.5s ease;
}

.bottom,
.top {
  height: 1px;
  left: 0;
  right: 0;
  transform: scaleX(0);
}

.left,
.right {
  width: 1px;
  top: 0;
  bottom: 0;
  transform: scaleY(0);
}

.bottom {
  bottom: 0;
  transform-origin: bottom right;
}

input:focus ~ .bottom {
  transform-origin: bottom left;
  transform: scaleX(1);
}

.right {
  right: 0;
  transform-origin: top right;
}

input:focus ~ .right {
  transform-origin: bottom right;
  transform: scaleY(1);
}

.top {
  top: 0;
  transform-origin: top left;
}

input:focus ~ .top {
  transform-origin: top right;
  transform: scaleX(1);
}

.left {
  left: 0;
  transform-origin: bottom left;
}

input:focus ~ .left {
  transform-origin: top left;
  transform: scaleY(1);
}
</style>
